






























































export default {
	data() {
		return {
			title: 'getSystemInfo',
			systemInfo: {}
		}
	},
	onUnload:function(){
		this.systemInfo = {};
	},
	methods: {
		getSystemInfo: function () {
			uni.getSystemInfo({
				success: (res) => {
					this.systemInfo = res
				}
			})
		}
	}
}
